<template>
<div>
    <div v-for="item in list" :key="item.key">
        <p>{{ item.id }}</p>
        <el-button @click="click(item.id)">success</el-button>
    </div>
</div>
</template>

<script>
import {
    getAllUnhandleInfo,
    handleWithdrawal
} from '@/api/billAPI';
export default {
    async mounted() {
        let ret = await getAllUnhandleInfo();
        this.list = ret.data;
        console.log(this.list);
    },
    data() {
        return {
            list: [],
        };
    },
    methods: {
        click(id) {
            const obj = {
                id: id,
                success_time: new Date(),
            };
            handleWithdrawal(obj);
        },
    },
};
</script>

<style lang="scss" scoped></style>
